.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 14px;
  left: 58px;
  width: 100%;
  z-index: 10;
}

nav {
  background-color: #fff;
  opacity: .8;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  text-transform: uppercase;
  text-align: center;
}

.link{
  text-decoration: none;
  font-size: 15px;
  //color: #fff;
  color: #3d4451;
  margin: 10px;
  text-align: center;
}

.link:hover{
  text-decoration: none;
  //color: #fff;
  color: #3d4451;
}

.link span{
  position: relative;
}

.link span:hover:before {
  position:absolute;
  content: '';
  bottom:0;
  width: 100%;
  height: 3px;
  background: #e9548d;
}

.isCloseMenuLogo, .isOpenMenuLogo {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 45px;
  height: 55px;
  top: 8px;
  left: 10px;
  cursor: pointer;
  z-index: 666;
}

.isOpenMenuLogo {
  transform: rotate(90deg);
}

@media screen and (max-width: 695px) {
  .link {
    font-size: 15px;
    margin: 5px;
  }
}

@media screen and (max-width: 630px) {
  .container {
    left: 49px;
  }

  nav{
    grid-template-columns: repeat(5,1fr);
  }

  .link {
    font-size: 15px;
    margin: 5px 3px;
  }

  .isCloseMenuLogo, .isOpenMenuLogo {
    width: 35px;
    height: 45px;
    top: 23px;
  }
}

@media screen and (max-width: 530px) {

  nav{
    grid-template-columns: repeat(3,1fr);
  }
}

@media screen and (max-width: 400px) {
  .isCloseMenuLogo, .isOpenMenuLogo {
    width: 30px;
    height: 40px;

  }
}



